@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background: rgb(100, 0, 123);
  background: radial-gradient(
    circle,
    rgba(100, 0, 123, 1) 0%,
    rgba(62, 20, 86, 1) 100%
  );
  overflow-x: hidden;
}

.bg-gradient-radial-circle {
  background-image: radial-gradient(
    circle,
    rgba(100, 0, 123, 1) 0%,
    rgba(62, 20, 86, 1) 100%
  );
}
